@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
    transform: translateY(0);
  }

  to {
    opacity: 0;
    transform: translateY(20px);
  }
}

.animate-fade-in {
  animation: .3s forwards fadeIn;
}

.animate-fade-out {
  animation: .3s forwards fadeOut;
}

.card {
  transition: transform .3s, box-shadow .3s;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px #0003;
}
